* {
  margin: 0;
  padding: 0;
}

body {
  margin: 0;
}

:root {
  color: #060b35;
  font-family: Roboto, sans-serif;
  font-size: 10px;
  font-weight: 400;
  line-height: 18px;
}

.header {
  box-sizing: border-box;
  grid-area: header;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  height: 60px;
  padding: 0 50px;
  display: flex;
}

.header__logo {
  width: 40px;
  height: 40px;
  display: block;
}

.header__nav-list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.header__nav-item {
  margin-left: 20px;
}

.header__nav-item:first-child {
  margin-left: 0;
}

.header__nav-link {
  color: #060b35;
  text-transform: uppercase;
  align-items: center;
  height: 60px;
  font-size: 12px;
  font-weight: 500;
  line-height: 15px;
  text-decoration: none;
  transition-duration: .3s;
  display: flex;
  position: relative;
}

.header__nav-link:hover, .header__nav .is-active {
  color: #00acdc;
}

.header__nav .is-active:after {
  content: "";
  border-bottom: 4px solid #00acdc;
  border-radius: 8px;
  width: 100%;
  display: flex;
  position: absolute;
  bottom: 0;
}

@media (width >= 488px) {
  .header__nav .header {
    width: 488px;
  }
}

@media (width >= 768px) {
  .header__nav .header {
    width: 768px;
  }
}

@media (width: 1024px) {
  .header__nav .header {
    width: 1024px;
  }
}

.catalog {
  box-sizing: border-box;
  grid-template-columns: repeat(1, 200px);
  justify-content: center;
  gap: 46px 48px;
  padding: 50px 40px;
  display: grid;
}

@media (width >= 488px) {
  .catalog {
    grid-template-columns: repeat(2, 200px);
  }
}

@media (width >= 768px) {
  .catalog {
    grid-template-columns: repeat(3, 200px);
  }
}

@media (width >= 1024px) {
  .catalog {
    grid-template-columns: repeat(4, 200px);
  }
}

@media (width: 1024px) {
  .catalog {
    width: 1024px;
  }
}

.catalog__card {
  box-sizing: border-box;
  background: #fff;
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  width: 200px;
  padding: 32px 16px 16px;
  transition-duration: .3s;
}

.catalog__card-image {
  width: 160px;
  height: 134px;
  margin: 0 auto 40px;
  display: block;
}

.catalog__card-title {
  margin: 0 0 4px;
  font-size: 12px;
  font-weight: 500;
}

.catalog__card-code {
  color: #616070;
  margin: 0 0 16px;
  padding: 0;
  line-height: 14px;
  display: flex;
}

.catalog__card-reviews {
  line-height: 14px;
}

.catalog__card-rating {
  justify-content: space-between;
  align-items: flex-end;
  margin-bottom: 24px;
  display: flex;
}

.catalog__card-stars {
  margin: 0;
  display: flex;
}

.catalog__card-stars-star {
  background-image: url("star.822d561e.svg");
  background-position: center;
  background-repeat: no-repeat;
  width: 16px;
  height: 16px;
  margin-right: 4px;
}

.catalog__card-stars-star:nth-child(-n+4) {
  background-image: url("star-active.edda4c43.svg");
}

.catalog__card-stars-star:last-child {
  margin: 0;
}

.catalog__card-price-container {
  justify-content: space-between;
  align-items: center;
  margin-bottom: 16px;
  display: flex;
}

.catalog__card-price-label {
  color: #616070;
  font-size: 12px;
}

.catalog__card-price-amount {
  font-size: 16px;
  font-weight: 700;
}

.catalog__card-button {
  box-sizing: border-box;
  color: #fff;
  text-transform: uppercase;
  background-color: #00acdc;
  border: 1px solid #00acdc;
  border-radius: 5px;
  justify-content: center;
  align-items: center;
  height: 40px;
  font-size: 14px;
  font-weight: 700;
  line-height: 16px;
  text-decoration: none;
  transition-duration: .3s;
  display: flex;
}

.catalog__card-button:hover {
  color: #00acdc;
  background-color: #fff;
}

.catalog__card:hover {
  transform: scale(1.2);
}

.catalog__card:hover .catalog__card-title {
  color: #34568b;
}
